import React,{useState} from 'react';
import "./App.css"
import AllDocViewer from './component/all-doc-viewer'
// import AllDocViewer from 'all-doc-viewer-fixed';





export default function App() {

        const [url,setUrl] = useState("https://file-examples-com.github.io/uploads/2017/08/file_example_PPT_500kB.ppt");
        const [h,setHeight] = useState('550');
        const [w,setWidth] = useState('720');
        
        const [rurl,setRUrl] = useState(url);
        const [rheight,setRHeight] = useState(w);
        const [rwidth,setRWidth] = useState(h);
        

        const update = ()=>{
            setUrl(rurl);
            setHeight(rheight);
            setWidth(rwidth);
        }

 
        return (
            <React.Fragment>
                <div className="block-left">
                    <div class="full-screen-container">
                        <div class="login-container">
                            <h3 class="login-title">Welcome</h3>
                            <form>
                                <div class="input-group">
                                    <label>URL</label>
                                    <input type="text" defaultValue={url} onChange={(e) => setRUrl(e.target.value)} />
                                </div>
                                <div class="input-group">
                                    <label>Height (in px)</label>
                                    <input type="text" defaultValue={h} onChange={(e) => setRHeight(e.target.value)} />
                                </div>
                                <div class="input-group">
                                    <label>Width (in px)</label>
                                    <input type="text" defaultValue={w} onChange={(e) => setRWidth(e.target.value)} />
                                </div>
                                <button type="button" onClick={()=>update()}  class="login-button">Load</button>
                            </form>
                            <div className="footer-badge">
                                <a href="https://www.npmjs.com/package/all-doc-viewer-fixed" target="_blank">&#10939; GO TO NPM</a>
                            </div>
                        </div>
                    </div>

                </div>
                <div className="block-right">
                    <AllDocViewer url={url} height={h} width={w} />
                </div>


            </React.Fragment>
        );
}